body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  font-family: 'Roboto Condensed', sans-serif;
} 

.not-found {
  height: 80vh;
  margin-left: 15px;
}

h1, h2, h3, button, a {
  font-family: 'Special Elite', cursive;
}

.page-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}


button {
  margin: 5px;
  padding-top: 5px;
  text-align: center;
  width: 140px;
  border-radius: 5px;
  font-size: 20px;
  background-color: darkgray;
  cursor: pointer;
} 

#navContainer {
  background-color: lightgray;
  display: flex;
  justify-content: center;
  width: 100%;
  height: 100%;
}

#navbar {
  display: flex;
  justify-content: space-around;
  height: 50px;
  width: 200px;
  align-items: center; 
}

.link {
  -webkit-text-decoration-line: none;
          text-decoration-line: none;
  color: black;
  font-weight: bold;
  font-size: 25px;
  padding-top: 7px;
  padding-left: 10px;
  padding-right: 10px;
}

.active {
  border: 5px solid  rgba(75, 76, 77, 0.52);
  border-radius: 5px;
}

.flex {
  display: flex;
}



.project-banner {
  position: relative;
  height: 350px;
  width: 100%;
  background-image: 
    url(/static/media/laptop2.8c44a344.jpg);
  background-size: cover;
  background-position: center;
  color: black;
}

#overview {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 33%;
  margin-left: 2%;
  height: 100%;
  overflow: auto;
  /* border: 2px dotted green; */
}

#overview h1 {
 text-align: center;
 margin-top: 15px;

}
#overview p {
  text-align: left;
  font-size: 22px;
}

#overview a {
  text-align: center;
}

.images-container {
  height: auto;
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  margin-bottom: 15px;
}


.image-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  overflow: auto;
  max-height: 50vh;
  width: 48%;
  min-width: 350px; 
  margin-top: 15px;
  border: 3px solid darkgray;
  border-radius: 5px;
}

.images-container img {
  height: auto;
  width: 100%;
}

@media only screen and (max-width: 800px) {
  .project-banner {
    background-image: none;
    height: auto;
  }
  
  #overview {
    width: 100%;
  }

  #overview p {
    margin-right: 15px;
    margin-left: 15px;
    overflow: scroll;
  }
  .images-container img {
  height: 100%;
  width: 100%;
  align-self: center;
}
}


 #chile {
   position: absolute;
   z-index: 2;
   left: 10px;
}

.work {
  display: flex; 
  align-items: top;
  background-image: 
    url(/static/media/laptop.f2376efe.jpg);
  background-size: cover;
  background-position: center;
  height: 350px;
  width: 100%;
}

.projects-overview {
  position: relative;
  color: white;
  width: 25%;
  max-height: 350px;
  border-radius: 10px;
  margin: 10px;
  overflow: auto;
}

.projects-overview h1 {
  position: absolute;
  height: auto;
  width: 100%;
  text-align: center;
}

.projects-overview p {
  position: absolute;
  height: auto;
  width: 95%;
  font-size: 22px;
  top: 35px;
  padding-left: 15px;
}

.projects {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  /* width: 100%; */
  width: auto;
  padding-bottom: 3%;
  padding-top: 2%;
}

.project-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 290px;
  width: 290px;
  border: 2px solid rgba(75, 76, 77, 0.52);
  border-radius: 30px;
  margin-top: 10px;
}

.project-card:hover {
  box-shadow: 10px 10px 10px rgba(75, 76, 77, 0.52);
  transition: box-shadow ease-in .5s;
}

.project-img {
 overflow: hidden;
 width: 100%;
 height: 250px;
 border-top-left-radius: 30px;
 border-top-right-radius: 30px; 
}

.project-title {
  border-top: 2px solid rgba(75, 76, 77, 0.52);
  width: 100%;
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;  
}

.project-title h2 {
  text-align: center;
  height: 40px;
}


.projects a {
  text-decoration: none;
  color: black;
} 

@media only screen and (max-width: 825px) {
  .work {
    background-image: none;
    height: 150px;
  }

  .projects-overview {
    width: 100%;
    color: black
  }

  .projects {
    top: 45%;
  }
}

@media only screen and (min-width: 1485px) {
  .projects {
   margin-left: 12%;
   margin-right: 12%;
  }
}
 #home {
  position: absolute;
  height: 700px;
  width: 100%;
  background-image: 
    url(/static/media/bariloche.834053da.jpg);
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
}

.shade {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 40%;
  height: 83vh;
  background-image: linear-gradient(to top, rgba(76, 199, 86, 0.52), rgba(9, 101, 240, 0.73));
  border-right: 5px solid rgba(245, 246, 252, 0.52);
  color: white;
}

.avatar {
  margin-top: 15%;
  min-height: 250px;
  width: 250px;
  border:5px solid rgba(245, 246, 252, 0.52);
  border-radius: 15px;
}

#welcome {
  text-align: center;
  margin: 5%;
}

#welcome h1 {
  font-size: 70px;
  margin: 0;
}

#welcome p {
  font-size: 25px;
  margin: 0;
  margin-bottom: 15px;
}

@media only screen and (max-width: 850px) {
  .shade {
    width: 100%;
  }
}
.map-container {
  width: 100%;
  position: absolute;
}

.map {
  height: 350px;
  width: 100%;
  background-image: 
    url(/static/media/airplane-map.159f6b12.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.about-container {
  width: 100%;
  height: auto;
  display: flex;
  position: relative;
  justify-content: center;
  margin-top: 65px;
}

.about-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 60%;
  height: auto;
}

.headshot {
  align-self: flex-end;
  height: 250px;
  width: 250px;
  background-image: url(/static/media/headshot.3b564341.jpeg);
  background-size: cover;
  border: 5px solid lightgrey;
  margin-right: 25px;

  border-radius: 15px;
}

.about {
  padding-top: 2%;
}

.about-wrapper p {
  font-size: 25px;
}

.contacts a {
  margin: 8px;
}

.contacts {
  width: 50%;
  text-align: center;
  margin-bottom: 10px;
}

@media only screen and (max-width: 800px) {
  .map {
    height: 250px;
  }

  .about-wrapper{
    width: 100%;
    margin-left: 5px;
    margin-right: 5px;
    padding-left: 10px;
    padding-right: 5px;
  }

  .headshot {
    height: 150px;
    width: 150px;
  }
  
  .contacts {
    width: 100%;
  }
}
.footer {
  border-top: 2px  solid lightgray;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-items: baseline;
  flex-grow: 1;
  background-color: lightgray;
}

@media only screen and (max-width: 830px) {
  .footer p {
    padding-left: 2%;
    padding-right: 2%;
  }
}
